.Customer {
    width: 450px;
    margin: 20px auto;
    padding: 15px;
    background-color: #46529d;
    color: #fff;
}

.Customer h2 {
    text-align: center;
    font-size: 24px;
    font-weight: bolder;
}

.Customer p {
    margin: 15px 0;
}

.Customer p label {
    display: inline-block;
    width: 80px;
    text-align: center;
}

.Customer p input {
    width: 70%;
    height: 25px;
    padding: 5px 15px;
    border: 0;
}

.Customer p button {
    width: 70px;
    height: 30px;
    display: inline-block;
    background-color: #2ebaee;
    color: #fff;
    font-size: 20px;
    padding-top: 3px;
}

.Customer p button.editBtn {
    background-color: #2ebaee;
}

.Customer p button.canBtn {
    background-color: #d3614e;
}



.Customer p:last-child{
    text-align: center;
}

.Customer h2.users {
    margin-top: 30px;
    padding: 10px 0;
}

.Customer table {
    width: 100%;
}

.Customer table .name {
    width: 22%;
}

.Customer table .addr {
    width: 22%;
}

.Customer table .admin {}

.Customer table tr {
    background-color: #addaeb
}

.Customer table th,
.Customer table td {
    text-align: center;
    padding: 10px 0;
}

.Customer table th {
    color: #fff;
    background-color:#2ebaee
}

.Customer td button {
    width: 60px;
    height: 30px;
    margin-right: 15px;
    font-size: 24px;
    color: #fff;
}
.Customer td button.editBtn {
    background-color: #8dba83;
    
}
.Customer td button.delBtn {
    background-color: #d3614e;
} 

.Customer td button:last-child {
    margin-right: 0;
}
